        width: 100%;
        height: 2px;
        background-color: #616161; }

    .menu__btn > span::before {
        content: '';
        top: -8px; }

    .menu__btn > span::after {
        content: '';
        top: 8px; }


    .menu__box {
        z-index: 1;
        display: block;
        position: fixed;
        visibility: hidden;
        top: 0;
        left: -100%;
